function openFirstPanel(){
    $('.accordion:first-of-type > dt:first-of-type').addClass('active').next('dd').addClass('active'); // Open first panel from first Accordion (if there are more then one)
}

$(function() {
    openFirstPanel();

    $('.accordion > dt').click(function() {
        var $this = $(this),
            $target =  $this.next();

        if($this.hasClass('active')){
            $this.removeClass('active');
            $target.removeClass('active').addClass('hidden');
        } else {
            $this.addClass('active');
            $target.addClass('active').removeClass('hidden');
        }

        return false;
    });

});