// Initialise Flickity-Slider
// IMPORTANT: FLICKITY.JS MUST BE INCLUDED IN THE SCRIPTS.JS
jQuery('.main-carousel').flickity({
    // options
    cellAlign: 'left',
    contain: true,
    imagesLoaded: true,
    dragThreshold: 30
});


// resize after first image of the slider was loaded
$('.main-carousel .carousel-cell:first-child img').load(function () {
    $(this).parent('.main-carousel').flickity('resize');
});