// read out cookie
function getCookie(cname) {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(';');
    for(var i = 0; i <ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
        }
    }
    return "";
}

$.ajax({
    type: "GET",
    url: "/wp-content/themes/template/json/smr_news.json",
    crossDomain: true,
    cache: false,
    dataType: "json",
    contentType: "application/json; charset=UTF-8",
    success: function(response) {
        var jsonLength = response.length, // number of articles in the JSON
            cookieVisit = 'smr_user_last_visit', // name of the cookie to save in the value (jsonLength)
            cookieClick = 'smr_user_clicked_news',
            readVisit = getCookie(cookieVisit),
            readClick = getCookie(cookieClick),
            bell = document.getElementById('smr_notification-bell');


        if ( readVisit == '' ) {
            // FIRST TIME VISIT
            console.log('new user. we got news!');
            bell.classList.add('update');

        } else if ( readVisit != '' && readVisit < jsonLength ) {

            // WE KNOW HIM AND WE GOT A NEW ARTICLE
            console.log('known user and we got at least one new article for him.');
            bell.classList.add('update');

        } else if ( readVisit != '' && readVisit == jsonLength ) {

            // WE KNOW HIM, BUT WE GOT NOTHING NEW TO SHOW
            console.log('known user but nothing new here. lets remove the update-class then..');
            if ( bell.classList.contains('update')) {
                bell.classList.remove('update');
            }

        }

        // if user clicks the bell -> set cookie
        bell.addEventListener("click", function() {

            // SET COOKIE WITH THE NUMBER OF ARTICLES IN THE JSON
            // SAVE IT FOR 30 DAYS
            var date = new Date(),
                days = 30; // expire after 60 days

            date.setTime(date.getTime()+(days*24*60*60*1000));
            var expiration = date.toGMTString();

            document.cookie = cookieVisit + '=' + jsonLength + ';expires='+ expiration +'; path=/';
        })



    },
    error: function (xhr, textStatus, errorThrown) {
        console.log(errorThrown);
    }
});